const app = getApp()
var QQMapWX = require('@/libs/qqmap-wx-jssdk.min.js')
var qqmapsdk = new QQMapWX({
	key: 'LXCBZ-NNIKD-UZ64F-H6AFI-UNJLH-OCFGE'
})
export default {
	data() {
		return {
			list: []
		}
	},
	created() {
		uni.setNavigationBarTitle({
			title: '搜索地址'
		})
	},
	methods: {
		searchList(n) {
			let that = this
			qqmapsdk.getSuggestion({
				keyword: n,
				success: res => {
					console.log(res)
					that.setData({
						list: res.data
					})
				},
				fail: err => {
					console.log(err)
				}
			})
		},
		bindInput(e) {
			let val = e.detail.value
			this.searchList(val)
		},
		bindConfirm(e) {
			let val = e.detail.value
			this.searchList(val)
		},
		address(item) {
			console.log(item);
			let pages = getCurrentPages()
			let prePages = pages[pages.length - 2].$vm
			
			console.log(prePages);
			let list = prePages.list
			let map = prePages.map
			map.longitude = item.location.lng
			map.latitude = item.location.lat
			prePages.address = item
			prePages.list = list
			prePages.map = map
			prePages.position = {
					longitude: item.location.lng,
					latitude: item.location.lat
				}
			prePages.checked = 0
			uni.navigateBack({
				delta: 1
			})
		},
		setData(obj) {
			Object.assign(this, obj)
		},
	}
}
